.latest {
    padding: 90px 0;
}

.btn-transparent {
    font-family: 'Akrobat', sans-serif;
    font-size: 22px;
    font-weight: 600;
    padding: 23px 50px;
    border: 1px solid $blue;
    background: transparent;
    color: $font;
    display: inline-flex;
    align-items: center;
    justify-content: center;

    &:hover {
        background: $blue;
        color: #fff;
    }

    &:hover .ic_btntr {
        fill: #fff;
    }
}

.ic_btntr {
    width: 45px;
    height: 20px;
    fill: $font;
}

.catalog_slider {
    margin: 0 -10px;
}

.lcard {
    margin: 10px;
    border: 1px solid #ececec;

    &__img {
        margin-bottom: 20px;
        display: block;
        transition: all .3s;

        img {
            width: 100%;
            height: 200px;
            object-fit: cover;
        }

        &:hover {
            opacity: .9;
        }

    }

    &__cat {
        display: flex;
        justify-content: space-between;
        padding: 0 15px;
        margin-bottom: 40px;
    }

    &__left {
        font-size: 11px;
        color: #5b5b5b;
    }

    &__right {
        color: #fff;
        padding: 4px 8px;
        border-radius: 3px;
        font-size: 11px;
    }

    &__right--green {
        background: #0da405;
    }

    &__right--red {
        background: #c76868;
    }

    &__title {
        display: block;
        font-family: 'Akrobat', sans-serif;
        font-size: 24px;
        color: $font;
        margin-bottom: 10px;
        padding: 0 15px;
        line-height: 1.3;
        height: 62px;
        overflow: hidden;

        &:hover {
            color: $blue;
        }
    }

    &__char {
        margin-bottom: 20px;

        ul {
            padding: 0 15px;
            color: #5b5b5b;
            line-height: 1.6;
            font-size: 14px;
        }
    }

    &__btn {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        background: $blue;
        color: #fff;
        padding: 25px 20px;
        font-family: 'Akrobat', sans-serif;
        font-size: 22px;
        text-transform: uppercase;

        .ic_latest {
            width: 30px;
            height: 20px;
            fill: #fff;
        }

        &:hover {
            background: darken($blue, 5%);
            color: #fff;
        }
    }

}

.ccard {
    margin: 10px 0px;
}

@media only screen and (max-width : 576px) {
    .btn-transparent {
        font-size: 18px;
        font-weight: 600;
        padding: 18px 30px;
        margin-bottom: 20px;
        width: 100%;

    }
}
