.header {
    position: relative;
    padding-top: 15px;
    z-index: 30;
    background: transparent;

    &__desc {
        font-size: 14px;
        color: #353535;
        line-height: 1.4;
    }

    &__input {
        padding: 16px;
        width: 100%;
        border-radius: 30px;
        background: #f2f3f7;
        border: none;
        height: 47px;

        &::placeholder {
            color: #cbcbcf;
        }
    }

    &__ic {
        width: 22px;
        height: 22px;
        fill: $blue;
    }

    &__btn {
        border: none;
        background: transparent;
        margin-left: -50px;
        transition: all .3s;
        cursor: pointer;
        opacity: .6;

        &:hover {
            opacity: 1;
        }
    }

    form {
        display: flex;
        width: 100%;
    }

    &__phone {
        display: flex;
        justify-content: flex-end;
    }

    .phone-icon {
        width: 47px;
        height: 47px;
        background: #f2f3f7;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        margin-right: 10px;
    }

    .ic_header {
        width: 14px;
        height: 19px;
        fill: $blue;
    }

    .phone-link {
        font-family: 'Akrobat', sans-serif;
        font-size: 26px;
        font-weight: 700;
        margin-bottom: 5px;
        color: #353535;
        display: block;
        letter-spacing: 1px;


        &:hover {
            color: #353535;

        }
    }

    .phone-call {
        font-size: 15px;
        font-weight: 300;
        padding-bottom: 3px;
        border-bottom: 1px dotted $blue;
        display: inline-flex;
        cursor: pointer;
    }

    &__menu {
        margin-top: 70px;
    }

}

.header-page {
    .header__menu {
        position: relative;
        margin-top: 33px;

        &::before {
            content: '';
            height: 70px;
            background: $blue;
            width: 300%;
            position: absolute;
            left: -1000px;
            top: -16px;


        }
    }

}

@media only screen and (max-width : 990px) {
    .header {
        background: #fff;
        padding: 0;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 200;

        &__menu {
            margin-top: 0px;
        }

        &__desc {
            font-size: 9px;
        }

        &__logo {
            img {
                max-width: 100px;
            }
        }
    }

    .toggle-mnu {
        margin-top: 5px !important;
    }

    .home-menu {
        display: flex;
        justify-content: flex-end;
    }

    .header-page .header__menu {
        position: relative;
        margin-top: 5px;
    }


}
