.round {
    position: absolute;
    width: 670px;
    height: 670px;
    bottom: -20%;
    border-radius: 50%;
    border: 120px solid #006384;
    display: block;
    z-index: 1;

}

.pseudo::after {
    content: '';
    position: absolute;
    width: 12000px;
    height: 1px;
    background: #fff;
    opacity: .4;
    right: 0;
    top: 90px;
}

.main-slider {
    position: relative;
    z-index: 10;
    background: $blue;
    margin-top: -90px;
    padding-top: 60px;
    height: 657px;
    overflow: hidden;

    h1 {
        font-family: "Akrobat", sans-serif;
        font-weight: 700;
        line-height: 1.4;
        font-size: 44px;
        color: #fff;
        padding-top: 80px;
        margin-bottom: 20px;
        padding-right: 60px;
        position: relative;
        z-index: 10;
    }

    p {
        color: #fff;
        font-weight: 300;
        font-size: 22px;
        line-height: 1.5;
        position: relative;
        z-index: 10;
    }

}

.slider-wrp {
    position: absolute;
    right: 0;
    top: 0;
    width: 45%;
    z-index: 10;
}

.relative {
    position: relative;
    z-index: 10;

    img {
        height: 657px;
        object-fit: cover;
    }
}

.btn-more {
    position: absolute;
    left: 0;
    bottom: 160px;
    color: #fff;
    background: #044162;
    padding: 30px 40px;
    transition: all .3s;
    font-family: 'Akrobat', sans-serif;
    font-size: 22px;
    font-weight: 600;

    .ic_btn {
        width: 21px;
        height: 15px;
        fill: #fff;
    }

    &:hover {
        background: darken(#044162, 5%);
        color: #fff;
    }
}

@media only screen and (max-width : 1208px) {
    .main-slider {
        h1 {
            font-size: 36px;
            line-height: 1.3;
        }

        p {
            font-size: 18px;
            padding-right: 40px
        }
    }

}

@media only screen and (max-width : 992px) {
    .main-slider {
        h1 {
            margin-top: 40px;
        }

        p {
            margin-bottom: 40px;
        }
    }

    .btn-more {
        bottom: 0px;
        right: 0;
        left: auto;
        color: #fff;
        padding: 15px 25px;
        font-size: 18px;
        font-weight: 400;

        .ic_btn {
            width: 21px;
            height: 15px;
            fill: #fff;
        }

    }
}

@media only screen and (max-width : 768px) {
    .main-slider {
        height: auto;
        width: 100%;
    }

    .slider-wrp {
        position: relative;
        width: 100%;
        height: auto;
    }

    .relative {
        position: relative;

        img {
            height: 400px;
            object-fit: cover;
            width: 100%;
        }
    }


}

@media only screen and (max-width : 576px) {
    .main-slider {
        h1 {
            font-size: 20px;
            padding-right: 0;

        }

        p {
            font-size: 14px;
            padding-right: 0;
            margin-bottom: 30px;
        }
    }


}
