.block-title {
    font-family: 'Akrobat', sans-serif;
    font-size: 47px;
    font-weight: 600;
    text-transform: uppercase;
    margin-bottom: 80px;

}

.block-txt {
    color: #5b5b5b;
    line-height: 1.5;
    margin-bottom: 30px;

}

.services {
    padding: 90px 0 0 0;

    &__slider {
        margin-right: -50%;
    }
}

.ser-sl {
    margin: 0 1px;
    position: relative;
    transition: all .3s;

    &:hover .ic_serv {
        padding-left: 10px;
        width: 30px;
    }

    &:hover {
        opacity: .9;
    }



    img {
        width: 100%;
        height: 431px;
        object-fit: cover;
    }
}

.service__info {
    position: absolute;
    left: 0;
    bottom: 0;
    padding: 60px 15px 40px 15px;
    background: linear-gradient(rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.8) 100%);
    width: 100%;
}

.service__title {
    font-family: 'Akrobat', sans-serif;
    font-size: 22px;
    color: #fff;
    margin-bottom: 20px;

}



.ic_serv {
    width: 20px;
    height: 12px;
    fill: #fff;
    transition: all .3s;
}

.service__lnk {
    color: #fff;
    opacity: .8;


    &:hover {
        opacity: 1;
        color: #fff;
    }

    &:hover .ic_serv {
        padding-left: 10px;
        width: 30px;
    }

}

.services__arrows,
.l-arrows {
    margin-bottom: 15px;

    .pr-prev {
        width: 50px;
        height: 50px;
        background: $blue;
        color: #fff;
        border: none;
        margin-right: 2px;
        transition: all .3s;

        &:hover {
            background: darken($blue, 5%)
        }
    }

    .pr-next {
        width: 50px;
        height: 50px;
        background: $blue;
        color: #fff;
        border: none;

        &:hover {
            background: darken($blue, 5%)
        }
    }

}


@media only screen and (max-width : 1200px) {
    .services {
        padding: 30px 0;

        &__slider {
            margin-right: 0;
        }
    }

    .block-title {
        font-size: 36px;
        margin-bottom: 40px;

    }

}

@media only screen and (max-width : 576px) {
    .block-txt {
        font-size: 14px;
    }

    .block-title {
        font-size: 22px;
        margin-bottom: 20px;

    }

    .ser-sl {

        img {
            width: 100%;
            height: 231px;
            object-fit: cover;
        }
    }
}
