.tizers {
    padding: 90px 0;
    background: #fcfcfc;
}

.tizer {
    margin-bottom: 20px;
    text-align: center;
}

.tizer__icon {
    width: 50px;
    height: 60px;
    fill: #c56363;
    margin-bottom: 30px;
}

.tizer__title {
    font-size: 17px;
    font-weight: 600;
    line-height: 1.4;

}

.tizer-dscr {
    margin-top: 30px;
    text-align: center;
}

@media only screen and (max-width : 576px) {
    .tizer__icon {
        margin-bottom: 15px;
    }

    .tizers {
        padding: 30px 0;
        background: #fcfcfc;
    }

    .tizer__title {
        font-size: 13px;
        font-weight: 600;
        line-height: 1.4;

    }

    .tizer-dscr {
        margin-top: 0px;
        text-align: left;
        margin-bottom: 0;
    }

}
