.slider-pic {
    display: flex;
    margin-top: -160px;
    position: relative;
    z-index: 60;
}

.slider-nav {
    flex-basis: 83%;
    position: relative;
    z-index: 90;

}

.slider-icon {
    display: flex;
    flex-basis: 17%;
    background: $blue;
    padding: 30px;
    justify-content: center;
    align-items: center;
    color: #fff;
    transition: all .3s;
    flex-direction: column;
    font-family: 'Akrobat', sans-serif;
    font-size: 19px;

    &:hover {
        background: darken($blue, 5%);
        color: #fff;
    }

    .ic_cat {
        width: 68px;
        height: 68px;
        fill: #fff;
        margin-bottom: 20px;
    }
}

.slick-list {
    padding: 0 !important;
}

.f20 {
    flex-basis: 20%;
    cursor: pointer;
}

.pic {
    height: 228px;
    padding: 30px 10px;
    background: #eef0f4;

    img {
        height: 112px;
        object-fit: cover;
        margin-bottom: 20px;
    }
}

.pic-dark {
    background: #e0e1eb;
}

.nav-title {
    font-size: 19px;
    font-weight: 600;
    font-family: "Akrobat", sans-serif;
}

@media only screen and (max-width : 992px) {
    .slider-pic {

        margin-top: 0px;
        flex-wrap: wrap;

    }

    .slider-icon {
        flex-basis: 100%;
        padding: 30px;
        justify-content: center;
        align-items: center;
        flex-direction: row;
        font-size: 19px;

        .ic_cat {
            width: 28px;
            height: 28px;
            fill: #fff;
            margin-bottom: 0px;
            margin-right: 15px;
        }
    }

    .slider-nav {
        flex-basis: 100%;

    }


}



@media only screen and (max-width: 768px) {
    * {
        min-height: 0;
        min-width: 0;
    }

    .slider-pic {
        overflow: hidden;
        z-index: 90;
        position: relative;

    }

    .f20 {
        flex-basis: 50%;
    }

    .arrows {
        position: absolute;
        z-index: 9999;
        bottom: 0;
    }

    .slick-prev3 {
        position: absolute;
        left: 0;
        bottom: 0;
        width: 52px;
        height: 48px;
        border: none;
        background: $blue;
        color: #fff;



        .fa {
            &::before {
                content: "\f104";
            }
        }
    }

    .slick-next3 {
        position: absolute;
        left: 53px;
        bottom: 0;
        width: 48px;
        height: 48px;
        border: none;
        background: $blue;
        color: #fff;


        .fa {
            &::before {
                content: "\f105";
            }
        }
    }


}
